* {
  box-sizing: border-box;
}

h1,
h2,
h3,
p,
ul {
  margin: 0;
  padding: 0;
}

a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
}

button,
input,
textarea,
select {
  font-family: Geometria, sans-serif;
  font-weight: 400;
  border: none;
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
}

ul li {
  list-style-type: none;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}


/*Var*/

@theme-color: #bef52b;
@grey-color: #323a45;
@dark-color: #252b33;
@text-color: #333;
@light-color: #ededed;


/*Fonts*/

@font-face {
  font-family: Geometria;
  font-weight: 300;
  src: url(../fonts/Geometria-Light.woff) format("woff");
  src: url(../fonts/Geometria-Light.ttf) format("truetype");
}

@font-face {
  font-family: Geometria;
  font-weight: 400;
  src: url(../fonts/Geometria-Medium.woff) format("woff");
  src: url(../fonts/Geometria-Medium.ttf) format("truetype");
}

@font-face {
  font-family: Geometria;
  font-weight: 500;
  src: url(../fonts/Geometria-Bold.woff) format("woff");
  src: url(../fonts/Geometria-Bold.ttf) format("truetype");
}

@font-face {
  font-family: Geometria;
  font-weight: 700;
  src: url(../fonts/Geometria-ExtraBold.woff) format("woff");
  src: url(../fonts/Geometria-ExtraBold.ttf) format("truetype");
}


/*Common*/

html {
  font-size: 16px;
}

body {
  font-family: Geometria, sans-serif;
  font-weight: 400;
  position: relative;
  margin: auto;
  color: @text-color;
  background-color: #fff;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.no-scroll {
  overflow: hidden;
}

.wrapper {
  width: 72rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}

.title {
  font-size: 2.3rem;
  width: 100%;
  font-weight: 500;
}

.button {
  font-weight: 500;
  font-size: 1rem;
  color: @text-color;
  text-align: center;
  background-color: @theme-color;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 3rem;
  min-width: 13rem;
  min-height: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  &:hover {
    background-color: #a8de2a;
  }
}

.swiper-arrow {
  width: 3.3rem;
  height: 3.3rem;
  background-color: @theme-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.4rem;
  border-radius: 50%;
  position: absolute;
  bottom: 5rem;
  transition: .3s;
  z-index: 3;
  cursor: pointer;
  &:not(.swiper-button-disabled):hover {
    background-color: #a8de2a;
  }
  &-prev {
    background-image: url(../img/arrow-prev.svg);
    right: ~'calc(50% - 29rem)';
  }
  &-next {
    background-image: url(../img/arrow-next.svg);
    right: ~'calc(50% - 36rem)';
  }
  &.swiper-button-disabled {
    cursor: auto;
  }
}


/*Header*/

.header {
  padding-top: 1.6rem;
  padding-bottom: 1.6rem;
  background-color: @dark-color;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 5;
  transition: .3s;
  &.scrolled {
    padding-top: .6rem;
    padding-bottom: .7rem;
  }
  &__wrapper {
    width: 92%;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 7.2rem;
    height: 2.8rem;
    background: url(../img/logo-header.png) no-repeat center / contain;
  }
  &__menu {
    flex-grow: 2;
    max-width: 48rem;
    ul {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    a {
      font-size: .87rem;
      color: #fff;
      transition: .3s;
      &:hover {
        opacity: .7;
      }
    }
  }
  &__phone {
    font-size: .87rem;
    color: #fff;
    padding-bottom: .2rem;
    position: relative;
    &::after {
      content: '';
      width: 0;
      height: 1px;
      background-color: @theme-color;
      position: absolute;
      left: 0;
      bottom: 0;
      transition: .3s;
    }
    &:hover {
      &::after {
        width: 100%;
      }
    }
  }
  &__button {
    background-color: transparent;
    border: 1px solid @theme-color;
    color: @theme-color;
    font-size: .87rem;
    padding-top: .6rem;
    padding-bottom: .6rem;
    min-width: 11rem;
    margin-left: 1.5rem;
    &:hover {
      background-color: @theme-color;
      color: @text-color;
    }
  }
  &__mobile {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    z-index: 2;
    margin-top: 3rem;
    .button {
      margin-left: 0;
      width: 100%;
      max-width: 15rem;
      padding-top: .9rem;
      padding-bottom: 1.2rem;
      margin-bottom: 1rem;
      font-size: 1.1rem;
    }
  }
  &__adress {
    font-size: 1.2rem!important;
  }
  .social {
    margin-top: 1.7rem;
  }
}

.btn-menu {
  display: none;
  text-align: center;
  height: 1.6rem;
  flex-direction: column;
  justify-content: space-between;
  z-index: 5;
  transition: .3s;
  z-index: 15;
  span {
    background-color: @theme-color;
    display: block;
    height: 3px;
    width: 2.2rem;
    position: relative;
    transition: .2s;
  }
  &.active {
    span {
      &:first-of-type {
        top: .6rem;
        transform: rotate(45deg);
      }
      &:nth-of-type(2) {
        display: none;
      }
      &:last-of-type {
        bottom: .8rem;
        transform: rotate(-45deg);
      }
    }
  }
}


/*Stocks*/

.stocks {
  background-color: @grey-color;
  padding-top: 10rem;
  padding-bottom: 5rem;
  &__wrapper {
    justify-content: space-between;
  }
  &__container {
    width: 67%;
    position: relative;
    overflow: hidden;
    border-radius: .5rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../img/stock1.jpg);
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background: linear-gradient(150deg, rgba(0,0,0,0), rgba(0,0,0,0) 70%, #222);
      background-repeat: no-repeat;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
  &__image {
    max-width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
  &__info {
    font-size: .75rem;
    padding: .5rem 1.8rem;
    border-radius: 3rem;
    position: absolute;
    top: 1.8rem;
    z-index: 2;
    &_date {
      font-weight: 700;
      background-color: @theme-color;
      left: 2.5rem;
    }
    &_other {
      background-color: #fff;
      text-transform: uppercase;
      font-weight: 500;
      letter-spacing: .05rem;
      right: 2.5rem;
    }
  }
  &__button {
    position: absolute;
    right: 2.5rem;
    bottom: 2.2rem;
    z-index: 2;
  }
  &__tabs {
    width: 30%;
    &-item {
      width: 100%;
      height: 10.5rem;
      position: relative;
      overflow: hidden;
      border-radius: .5rem;
      cursor: pointer;
      border: 1px solid transparent;
      transition: .3s;
      &:not(:last-of-type) {
        margin-bottom: 1.4rem;
      }
      &::after {
        content: '';
        width: 100%;
        height: 100%;
        background: linear-gradient(to bottom, #000, rgba(0,0,0,.4));
        opacity: .7;
        position: absolute;
        right: 0;
        bottom: 0;
        transition: .3s;
      }
      &.active {
        border-color: @theme-color;
        &::after {
          opacity: .2!important;
        }
        span {
          display: none;
        }
      }
      &:hover {
        &::after {
          opacity: .5;
        }
      }
      span {
        font-size: .7rem;
        font-weight: 500;
        letter-spacing: .05rem;
        color: #fff;
        position: absolute;
        top: 1rem;
        z-index: 2;
        &:first-of-type {
          left: 1rem;
        }
        &:last-of-type {
          right: 1rem;
          text-transform: uppercase;
        }
      }
    }
  }
  &__miniature {
    max-width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}


/*Services*/

.services {
  padding-top: 6rem;
  padding-bottom: 5rem;
  overflow: hidden;
  &__title {
    margin-bottom: 4rem;
  }
  &__tabs {
    width: 100%;
    margin-left: -1.1rem;
    margin-right: -1.1rem;
    margin-bottom: 3.5rem;
    &-item {
      font-size: .9rem;
      margin-left: 1.1rem;
      margin-right: 1.1rem;
      padding-bottom: .4rem;
      transition: .3s;
      position: relative;
      cursor: pointer;
      &::after {
        content: '';
        width: 0;
        height: 2px;
        background-color: @theme-color;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        transition: .3s;
      }
      &:hover {
        opacity: .7;
      }
      &.active {
        &::after {
          width: 100%;
        }
      }
    }
  }
  &__container {
    position: relative;
  }
  &__item {
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    position: absolute;
    left: 0;
    top: 0;
    &.active {
      visibility: visible;
      opacity: 1;
      position: static;
    }
    p {
      opacity: .8;
      font-size: .9rem;
      line-height: 150%;
      margin-bottom: 1.5rem;
    }
    span {
      display: block;
      font-weight: 500;
    }
  }
  &__text {
    width: 50%;
  }
  &__name {
    display: block;
    font-size: 1.7rem;
    font-weight: 700;
    margin-bottom: 2rem;
  }
  &__button {
    margin-top: 1rem;
  }
  &__slider {
    position: absolute;
    left: 55%;
    top: 50%;
    transform: translateY(-50%);
    margin-bottom: -1rem;
    >div {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 55rem;
    }
    &-item {
      width: 32%;
      height: 14rem;
      border-radius: .5rem;
      overflow: hidden;
      position: relative;
      margin-bottom: 1rem;
      cursor: pointer;
      &::after {
        content: '';
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.5) url(../img/expand.svg) no-repeat center / 15%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        transition: .3s;
        opacity: 0;
      }
      &:hover {
        &::after {
          opacity: 1;
        }
      }
    }
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      height: 110%;
    }
  }
}


/*Trainers*/

.trainers {
  background-color: @light-color;
  padding-top: 5rem;
  padding-bottom: 8rem;
  position: relative;
  &__title {
    margin-bottom: 4rem;
  }
  &__slider {
    margin-left: ~'calc(50% - 36rem)';
    &-item {
      width: 16.2rem;
      padding-bottom: 4rem;
      height: initial !important;
      overflow: hidden;
      &:hover {
        .trainers__slider-wrapper {
          background-color: @theme-color;
          box-shadow: 0 1rem 2rem -0.2rem rgba(0, 0, 0, .2);
        }
        .trainers__button {
          background-color: #fff;
          bottom: 1.5rem;
          opacity: 1;
        }
      }
    }
    &-wrapper {
      margin-left: .8rem;
      margin-right: .8rem;
      height: 100%;
      background-color: #fff;
      border-radius: .5rem;
      padding: 1.4rem 1.3rem 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      transition: .3s;
      cursor: pointer;
      overflow: hidden;
      position: relative;
    }
  }
  .swiper-wrapper {
    align-items: stretch!important;
  }
  &__info {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  &__name {
    width: 100%;
    font-size: 1.1rem;
    font-weight: 700;
  }
  &__job {
    width: 100%;
    font-size: .87rem;
    font-weight: 300;
    margin-bottom: .8rem;
  }
  &__button {
    position: absolute;
    left: 50%;
    bottom: -20%;
    transform: translateX(-50%);
    opacity: 0;
  }
}


/*Media*/

.media {
  padding-top: 5rem;
  padding-bottom: 5rem;
  &__wrapper {
    justify-content: space-between;
    align-items: flex-end;
    >div {
      &:first-of-type {
        width: 58%;
      }
      &:last-of-type {
        width: 32%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
      }
    }
  }
  &__title {
    margin-bottom: 4rem;
  }
  &__video {
    width: 100%;
    height: 23rem;
    background: url(../img/video-prev.jpg) no-repeat center / cover;
    position: relative;
    cursor: pointer;
    &-play {
      display: block;
      width: 4.2rem;
      height: 4.2rem;
      background: rgba(255,255,255,.2) url(../img/play.svg) no-repeat center / 1.3rem;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      transition: .3s;
      &:hover {
        width: 4.7rem;
        height: 4.7rem;
      }
    }
  }
  &__subtitle {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 1.5rem;
    line-height: 120%;
    font-weight: 700;
    text-align: right;
    padding-left: 2.1rem;
    background: url(../img/media-inst.svg) no-repeat left .4rem;
    background-size: 1.2rem;
    margin-bottom: 1.5rem;
    a {
      font-size: 1rem;
      color: @grey-color;
      transition: .3s;
      &:hover {
        color: @theme-color;
      }
    }
  }
  &__inst {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: -.5rem;
    &-item {
      display: block;
      width: ~'calc(33.333% - .6rem)';
      height: 7rem;
      margin: .3rem;
      position: relative;
      overflow: hidden;
    }
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      max-width: 110%;
    }
  }
}


/*Cards*/

.cards {
  padding-top: 3rem;
  padding-bottom: 8rem;
  position: relative;
  overflow: hidden;
  &__title {
    margin-bottom: 2rem;
  }
  &__slider {
    margin-left: ~'calc(50% - 36rem - 30px)';
    padding-left: 30px;
    &-item {
      width: 34.4rem;
      padding-bottom: 4rem;
      padding-top: 2rem;
      overflow: hidden;
      &:hover {
        .cards__slider-wrapper {
          background-color: #fff;
          box-shadow: 0 .5rem 2rem -0.2rem rgba(0, 0, 0, .2);
        }
        .cards__paragraph {
          span {
            color: #bef52b;
          }
        }
        .cards__button {
          background-color: #ddd;
          border-color: #ddd;
        }
      }
    }
    &-wrapper {
      margin-left: .8rem;
      margin-right: .8rem;
      background-color: @light-color;
      border-radius: .5rem;
      padding: 2.2rem 0 2.2rem 1.8rem;
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      transition: .3s;
      cursor: pointer;
      overflow: hidden;
    }
  }
  &__info {
    width: 48%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__paragraph {
    width: 49%;
    font-size: .8rem;
    line-height: 150%;
    color: #7a7d84;
    margin-bottom: 1rem;
    span {
      display: block;
      font-size: 1rem;
      font-weight: 500;
      color: @grey-color;
      transition: .3s;
    }
  }
  &__button {
    background-color: transparent;
    border: 1px solid @text-color;
    min-width: 70%;
    padding-top: .7rem;
    padding-bottom: .7rem;
  }
  &__cover {
    color: #fff;
    width: 50%;
    min-height: 11rem;
    border-radius: .5rem 0 0 .5rem;
    padding: 1.8rem .5rem 1.8rem 1.8rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    box-shadow: 1.5rem 1.5rem 2rem -.8rem rgba(0,0,0,0.23);
    position: relative;
    overflow: hidden;
    background-image: url(../img/logo-cards.png);
    background-size: 3rem;
    background-repeat: no-repeat;
    background-position: top 1.8rem left 1.8rem;
    &::before {
      content: '';
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, rgba(0,0,0,.1), rgba(0,0,0,0));
      position: absolute;
      left: 0;
      bottom: 0;
    }
    &::after {
      content: "";
      width: 3.9rem;
      height: 16rem;
      transform: rotate(45deg);
      position: absolute;
      background: linear-gradient(transparent, rgba(255,255,255,0.2));
      right: .7rem;
      bottom: -50%;
    }
  }
  &__name {
    font-size: 1.5rem;
    font-weight: 500;
    word-break: break-word;
    position: relative;
  }
  &__description {
    font-size: .87rem;
    font-weight: 300;
    position: relative;
    margin-top: 1rem;
  }
}


/*Gallery*/

.gallery {
  background-color: @light-color;
  padding-top: 5rem;
  padding-bottom: 8rem;
  position: relative;
  &__title {
    margin-bottom: 4rem;
  }
  &__slider {
    width: 100%;
    padding-bottom: 4rem;
    padding-left: 30px;
    &-item {
      width: 28rem;
      display: flex;
      flex-direction: column;
    }
  }
  &__image {
    width: 100%;
    height: 18.7rem;
    border-radius: .5rem;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    &:first-of-type {
      margin-bottom: 30px;
    }
    &:last-of-type {
      transform: translateX(-50%);
    }
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.5) url(../img/expand.svg) no-repeat center / 15%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      transition: .3s;
      opacity: 0;
    }
    &:hover {
      &::after {
        opacity: 1;
      }
    }
    img {
      max-height: 110%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      cursor: pointer;
    }
  }
}


/*About*/

.about {
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  &__wrapper {
    position: relative;
  }
  &__title {
    margin-bottom: 7.5rem;
  }
  &__image {
    width: 100%;
  }
  &__text {
    width: 60%;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 1rem;
    line-height: 150%;
    background-color: #fff;
    border-radius: .5rem;
    padding: 3rem;
    box-shadow: 0 1rem 2rem .3rem rgba(0, 0, 0, .2);
  }
}


/*Contact*/

.contact {
  padding-bottom: 5rem;
  position: relative;
  &__map {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
  #map {
    width: 100%;
    height: 100%;
  }
  .form {
    width: 28rem;
    padding: 3.8rem;
    border-radius: .5rem;
    margin-top: -6rem;
    &__title {
      font-size: 1.6rem;
      margin-bottom: 2rem;
    }
    &__button {
      width: 100%;
    }
  }
}


/*Footer*/

.footer {
  padding-top: 1.8rem;
  padding-bottom: 1.8rem;
  &__wrapper {
    justify-content: space-between;
    align-items: center;
    >p,
    >a:not(.footer__up) {
      padding-left: 2.8rem;
      background-repeat: no-repeat;
      background-position: left center;
      background-size: 1.4rem;
    }
    >a:first-of-type {
      background-image: url(../img/footer-adress.svg);
    }
    >p:nth-of-type(1) {
      background-image: url(../img/footer-time.svg);
    }
    >p:nth-of-type(2) {
      background-image: url(../img/footer-phone.svg);
    }
  }
  p {
    font-size: 1rem;
    line-height: 150%;
  }
  span, a {
    font-weight: 500;
    display: block;
  }
  a {
    color: @text-color;
    transition: .3s;
    &:hover {
      opacity: .7;
    }
  }
  &__up {
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    background: @theme-color url(../img/chevron-up.svg) no-repeat center / 1.1rem;
    border-radius: 50%;
  }
}

.social {
  display: flex;
  a {
    display: block;
    width: 2.2rem;
    height: 2.2rem;
    border-radius: 50%;
    margin-left: .7rem;
    margin-right: .7rem;
    transition: .3s;
    &:nth-of-type(1) {
      background: #3d5a96 url(../img/soc-fb.svg) no-repeat center / 1.4rem;
    }
    &:nth-of-type(2) {
      background: #4c77a6 url(../img/soc-vk.svg) no-repeat center / 1.1rem;
    }
    &:nth-of-type(3) {
      background: linear-gradient(180.19deg, #7024c4 0.17%, #c21975 41.57%, #c74c4d 70.15%, #e09b3d 99.83%);
      background-repeat: no-repeat;
      position: relative;
      &::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: url(../img/soc-inst.svg) no-repeat center / 1.1rem;
      }
    }
    &:hover {
      opacity: .7;
    }
  }
}


/*Forms*/

.form {
  position: relative;
  z-index: 3;
  &_dark {
    background-color: @grey-color;
    color: #fff;
  }
  &_light {
    background-color: #fff;
  }
  &__input {
    width: 100%;
    font-size: 1rem;
    color: @text-color;
    padding: 1rem 1.2rem;
    background-color: #fff;
    border: 1px solid #bbbbbb;
    border-radius: 3rem;
    &-wrap {
      display: block;
      width: 100%;
      position: relative;
      margin-bottom: 1.8rem;
      &.active {
        .form__input-text {
          font-size: .6rem;
          top: 20%;
          opacity: .7;
        }
      }
    }
    &-text {
      font-size: 1rem;
      color: @text-color;
      position: absolute;
      left: 1.2rem;
      top: 50%;
      transform: translateY(-50%);
      transition: .3s;
    }
  }
  &__agreement {
    width: 100%;
    display: block;
    margin-bottom: 1.8rem;
    &-text {
      font-size: .8rem;
      position: relative;
      padding-left: 2rem;
      cursor: pointer;
      user-select: none;
      a {
        color: #bfbfbf;
        text-decoration: underline;
      }
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 1rem;
        height: 1rem;
        border: 2px solid @theme-color;
        border-radius: 50%;
        background-color: transparent;
      }
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: .7rem;
        height: .7rem;
        border-radius: 50%;
        background: none;
      }
    }
    &-checkbox {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }
    .registrate__agreement-checkbox:checked + .registrate__agreement-text::after {
      background: @theme-color;
    }
  }
  &__button {
    cursor: pointer;
  }
}


/*Modal*/

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 12;
  background: rgba(0, 0, 0, 0.8);
  &__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
  }
  &__wrapper {
    background-color: #fff;
    border-radius: .5rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) scale(.3, .3);
    width: 32rem;
    max-height: 90vh;
    overflow: auto;
    justify-content: center;
    transition: .5s;
    opacity: 0;
    &::-webkit-scrollbar {
      width: 5px;
      -webkit-appearance: none;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: @theme-color;
      -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
    }
    &.active {
      opacity: 1;
      transform: translateX(-50%) translateY(-50%) scale(1, 1);
      z-index: 12;
    }
  }
  &_stocks {
    .modal__header {
      height: 18rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
    .modal__title {
      opacity: 0;
      transform: scale(0,0);
      height: 0;
    }
  }
  &_services,
  &_trainers {
    .modal__additional,
    .modal__additional-field {
      opacity: 1;
      transform: scale(1,1);
      height: initial;
    }
  }
  &_cards {
    .modal__title {
      margin-bottom: 2rem;
      text-align: center;
    }
    .modal__card {
      opacity: 1;
      transform: scale(1,1);
      min-height: 10.6rem;
      height: initial;
      padding: 1.8rem;
      display: flex;
    }
  }
  &__widget {
    max-height: 90vh;
    overflow: hidden;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%) scale(.3, .3);
    transition: .5s;
    opacity: 0;
    &.active {
      opacity: 1;
      transform: translateX(-50%) translateY(-50%) scale(1, 1);
      z-index: 12;
    }
  }
  &_video {
    width: 80%;
    max-width: 60rem;
    .modal__video {
      opacity: 1;
      transform: scale(1,1);
      height: initial;
    }
  }
  &_gallery {
    width: 80%;
    max-width: 60rem;
    .modal__slider {
      opacity: 1;
      height: initial;
    }
  }
  &__special {
    width: 100%;
    height: 100%;
    background-color: @light-color;
    overflow: auto;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateY(-300%);
    transition: .5s;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    &::-webkit-scrollbar {
      width: 5px;
      -webkit-appearance: none;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: @theme-color;
      -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
    }
    &::after {
      content: '';
      width: 7.2rem;
      height: 2.7rem;
      background: url(../img/logo-modal.png) no-repeat center / contain;
      position: absolute;
      left: 2rem;
      top: 2rem;
    }
    &.active {
      opacity: 1;
      transform: none;
      z-index: 12;
    }
    .modal__container {
      width: 100%;
      max-width: 75rem;
    }
    .modal__header {
      padding-bottom: 0;
      padding-top: 0;
    }
    .modal__title {
      font-size: 2.2rem;
      text-align: center;
      padding-bottom: 3.5rem;
      margin-bottom: 0;
    }
    &-row {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      margin-left: auto;
      margin-right: auto;
      padding-top: 3.5rem;
      padding-bottom: 3.5rem;
      border-top: 1px dashed @grey-color;
      border-bottom: 1px dashed @grey-color;
    }
    &-item {
      width: 31.5%;
      background-color: #fff;
      border-radius: .5rem;
      font-size: 1rem;
      font-weight: 300;
      line-height: 150%;
      padding: 2rem;
      cursor: pointer;
      border: 2px solid #fff;
      span {
        display: block;
        font-size: 1.5rem;
        font-weight: 500;
        margin-bottom: 1.2rem;
      }
      &.active {
        border-color: @theme-color;
        box-shadow: 0 .5rem 2rem -0.2rem rgba(0, 0, 0, .15);
      }
    }
    .modal__form {
      width: 100%;
      background-color: @light-color;
      flex-direction: row;
      justify-content: space-between;
      align-self: center;
      margin-bottom: 3rem;
    }
    .form__input {
      &-wrap {
        margin-bottom: 0;
        margin-right: 3%;
      }
    }
    .form__button {
      min-width: 30%;
      margin-bottom: 0!important;
    }
    .form__agreement {
      position: absolute;
      margin-bottom: 0;
      bottom: -3.5rem;
      left: 3.5rem;
      width: initial;
    }
  }
  &__header {
    background-color: @light-color;
    background-image: none;
    padding: 3.5rem;
  }
  &__title {
    font-size: 1.6rem;
    margin-bottom: 1.2rem;
    transition: .3s;
  }
  &__additional {
    display: block;
    opacity: 0;
    transform: scale(0,0);
    height: 0;
    transition: .3s;
    font-size: .87rem;
    font-weight: 500;
    color: #949494;
    margin-bottom: .5rem;
    &-field {
      display: block;
      opacity: 0;
      height: 0;
      transform: scale(0,0);
      transition: .3s;
      font-size: 1rem;
      font-weight: 500;
    }
  }
  &__card {
    opacity: 0;
    transform: scale(0,0);
    transition: .3s;
    width: 80%;
    display: none;
    height: 0;
    padding: 0;
    overflow: hidden;
    margin: 0 auto;
    border-radius: .5rem;
  }
  &__form {
    padding: 3.5rem 3.5rem 0 3.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .form__button {
      margin-bottom: 3.5rem;
    }
  }
  &__video {
    display: flex;
    width: 100%;
    opacity: 0;
    transform: scale(0,0);
    height: 0;
  }
  &__slider {
    opacity: 0;
    height: 0;
    position: relative;
    &-item {
      overflow: hidden;
      height: 40rem;
      width: 100%!important;
      border-radius: .5rem;
      display: flex;
      justify-content: center;
    }
    img {
      min-height: 100%;
      max-height: 110%;
    }
  }
  &__arrow {
    top: 50%;
    transform: translateY(-50%);
    &-prev {
      left: 2rem;
    }
    &-next {
      right: 2rem;
    }
  }
  &__close {
    display: block;
    width: 1.6rem;
    height: 1.6rem;
    background: rgba(0,0,0,.3) url(../img/close.svg) no-repeat center / 1rem;
    border-radius: 50%;
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
    z-index: 2;
  }
}