* {
  box-sizing: border-box;
}
h1,
h2,
h3,
p,
ul {
  margin: 0;
  padding: 0;
}
a,
a:hover,
a:active,
a:focus {
  text-decoration: none;
}
button,
input,
textarea,
select {
  font-family: Geometria, sans-serif;
  font-weight: 400;
  border: none;
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
}
ul li {
  list-style-type: none;
}
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}
/*Var*/
/*Fonts*/
@font-face {
  font-family: Geometria;
  font-weight: 300;
  src: url(../fonts/Geometria-Light.woff) format("woff");
  src: url(../fonts/Geometria-Light.ttf) format("truetype");
}
@font-face {
  font-family: Geometria;
  font-weight: 400;
  src: url(../fonts/Geometria-Medium.woff) format("woff");
  src: url(../fonts/Geometria-Medium.ttf) format("truetype");
}
@font-face {
  font-family: Geometria;
  font-weight: 500;
  src: url(../fonts/Geometria-Bold.woff) format("woff");
  src: url(../fonts/Geometria-Bold.ttf) format("truetype");
}
@font-face {
  font-family: Geometria;
  font-weight: 700;
  src: url(../fonts/Geometria-ExtraBold.woff) format("woff");
  src: url(../fonts/Geometria-ExtraBold.ttf) format("truetype");
}
/*Common*/
html {
  font-size: 16px;
}
body {
  font-family: Geometria, sans-serif;
  font-weight: 400;
  position: relative;
  margin: auto;
  color: #333;
  background-color: #fff;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.no-scroll {
  overflow: hidden;
}
.wrapper {
  width: 72rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}
.title {
  font-size: 2.3rem;
  width: 100%;
  font-weight: 500;
}
.button {
  font-weight: 500;
  font-size: 1rem;
  color: #333;
  text-align: center;
  background-color: #bef52b;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 3rem;
  min-width: 13rem;
  min-height: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
}
.button:hover {
  background-color: #a8de2a;
}
.swiper-arrow {
  width: 3.3rem;
  height: 3.3rem;
  background-color: #bef52b;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.4rem;
  border-radius: 50%;
  position: absolute;
  bottom: 5rem;
  transition: .3s;
  z-index: 3;
  cursor: pointer;
}
.swiper-arrow:not(.swiper-button-disabled):hover {
  background-color: #a8de2a;
}
.swiper-arrow-prev {
  background-image: url(../img/arrow-prev.svg);
  right: calc(50% - 29rem);
}
.swiper-arrow-next {
  background-image: url(../img/arrow-next.svg);
  right: calc(50% - 36rem);
}
.swiper-arrow.swiper-button-disabled {
  cursor: auto;
}
/*Header*/
.header {
  padding-top: 1.6rem;
  padding-bottom: 1.6rem;
  background-color: #252b33;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 5;
  transition: .3s;
}
.header.scrolled {
  padding-top: .6rem;
  padding-bottom: .7rem;
}
.header__wrapper {
  width: 92%;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: block;
  width: 7.2rem;
  height: 2.8rem;
  background: url(../img/logo-header.png) no-repeat center / contain;
}
.header__menu {
  flex-grow: 2;
  max-width: 48rem;
}
.header__menu ul {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__menu a {
  font-size: .87rem;
  color: #fff;
  transition: .3s;
}
.header__menu a:hover {
  opacity: .7;
}
.header__phone {
  font-size: .87rem;
  color: #fff;
  padding-bottom: .2rem;
  position: relative;
}
.header__phone::after {
  content: '';
  width: 0;
  height: 1px;
  background-color: #bef52b;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: .3s;
}
.header__phone:hover::after {
  width: 100%;
}
.header__button {
  background-color: transparent;
  border: 1px solid #bef52b;
  color: #bef52b;
  font-size: .87rem;
  padding-top: .6rem;
  padding-bottom: .6rem;
  min-width: 11rem;
  margin-left: 1.5rem;
}
.header__button:hover {
  background-color: #bef52b;
  color: #333;
}
.header__mobile {
  display: none;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  z-index: 2;
  margin-top: 3rem;
}
.header__mobile .button {
  margin-left: 0;
  width: 100%;
  max-width: 15rem;
  padding-top: .9rem;
  padding-bottom: 1.2rem;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}
.header__adress {
  font-size: 1.2rem!important;
}
.header .social {
  margin-top: 1.7rem;
}
.btn-menu {
  display: none;
  text-align: center;
  height: 1.6rem;
  flex-direction: column;
  justify-content: space-between;
  z-index: 5;
  transition: .3s;
  z-index: 15;
}
.btn-menu span {
  background-color: #bef52b;
  display: block;
  height: 3px;
  width: 2.2rem;
  position: relative;
  transition: .2s;
}
.btn-menu.active span:first-of-type {
  top: .6rem;
  transform: rotate(45deg);
}
.btn-menu.active span:nth-of-type(2) {
  display: none;
}
.btn-menu.active span:last-of-type {
  bottom: .8rem;
  transform: rotate(-45deg);
}
/*Stocks*/
.stocks {
  background-color: #323a45;
  padding-top: 10rem;
  padding-bottom: 5rem;
}
.stocks__wrapper {
  justify-content: space-between;
}
.stocks__container {
  width: 67%;
  position: relative;
  overflow: hidden;
  border-radius: .5rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../img/stock1.jpg);
}
.stocks__container::after {
  content: '';
  width: 100%;
  height: 100%;
  background: linear-gradient(150deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 70%, #222);
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  bottom: 0;
}
.stocks__image {
  max-width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.stocks__info {
  font-size: .75rem;
  padding: .5rem 1.8rem;
  border-radius: 3rem;
  position: absolute;
  top: 1.8rem;
  z-index: 2;
}
.stocks__info_date {
  font-weight: 700;
  background-color: #bef52b;
  left: 2.5rem;
}
.stocks__info_other {
  background-color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: .05rem;
  right: 2.5rem;
}
.stocks__button {
  position: absolute;
  right: 2.5rem;
  bottom: 2.2rem;
  z-index: 2;
}
.stocks__tabs {
  width: 30%;
}
.stocks__tabs-item {
  width: 100%;
  height: 10.5rem;
  position: relative;
  overflow: hidden;
  border-radius: .5rem;
  cursor: pointer;
  border: 1px solid transparent;
  transition: .3s;
}
.stocks__tabs-item:not(:last-of-type) {
  margin-bottom: 1.4rem;
}
.stocks__tabs-item::after {
  content: '';
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, #000, rgba(0, 0, 0, 0.4));
  opacity: .7;
  position: absolute;
  right: 0;
  bottom: 0;
  transition: .3s;
}
.stocks__tabs-item.active {
  border-color: #bef52b;
}
.stocks__tabs-item.active::after {
  opacity: .2!important;
}
.stocks__tabs-item.active span {
  display: none;
}
.stocks__tabs-item:hover::after {
  opacity: .5;
}
.stocks__tabs-item span {
  font-size: .7rem;
  font-weight: 500;
  letter-spacing: .05rem;
  color: #fff;
  position: absolute;
  top: 1rem;
  z-index: 2;
}
.stocks__tabs-item span:first-of-type {
  left: 1rem;
}
.stocks__tabs-item span:last-of-type {
  right: 1rem;
  text-transform: uppercase;
}
.stocks__miniature {
  max-width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
/*Services*/
.services {
  padding-top: 6rem;
  padding-bottom: 5rem;
  overflow: hidden;
}
.services__title {
  margin-bottom: 4rem;
}
.services__tabs {
  width: 100%;
  margin-left: -1.1rem;
  margin-right: -1.1rem;
  margin-bottom: 3.5rem;
}
.services__tabs-item {
  font-size: .9rem;
  margin-left: 1.1rem;
  margin-right: 1.1rem;
  padding-bottom: .4rem;
  transition: .3s;
  position: relative;
  cursor: pointer;
}
.services__tabs-item::after {
  content: '';
  width: 0;
  height: 2px;
  background-color: #bef52b;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  transition: .3s;
}
.services__tabs-item:hover {
  opacity: .7;
}
.services__tabs-item.active::after {
  width: 100%;
}
.services__container {
  position: relative;
}
.services__item {
  visibility: hidden;
  opacity: 0;
  transition: .3s;
  position: absolute;
  left: 0;
  top: 0;
}
.services__item.active {
  visibility: visible;
  opacity: 1;
  position: static;
}
.services__item p {
  opacity: .8;
  font-size: .9rem;
  line-height: 150%;
  margin-bottom: 1.5rem;
}
.services__item span {
  display: block;
  font-weight: 500;
}
.services__text {
  width: 50%;
}
.services__name {
  display: block;
  font-size: 1.7rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.services__button {
  margin-top: 1rem;
}
.services__slider {
  position: absolute;
  left: 55%;
  top: 50%;
  transform: translateY(-50%);
  margin-bottom: -1rem;
}
.services__slider > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 55rem;
}
.services__slider-item {
  width: 32%;
  height: 14rem;
  border-radius: .5rem;
  overflow: hidden;
  position: relative;
  margin-bottom: 1rem;
  cursor: pointer;
}
.services__slider-item::after {
  content: '';
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5) url(../img/expand.svg) no-repeat center / 15%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: .3s;
  opacity: 0;
}
.services__slider-item:hover::after {
  opacity: 1;
}
.services__slider img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  height: 110%;
}
/*Trainers*/
.trainers {
  background-color: #ededed;
  padding-top: 5rem;
  padding-bottom: 8rem;
  position: relative;
}
.trainers__title {
  margin-bottom: 4rem;
}
.trainers__slider {
  margin-left: calc(50% - 36rem);
}
.trainers__slider-item {
  width: 16.2rem;
  padding-bottom: 4rem;
  height: initial !important;
  overflow: hidden;
}
.trainers__slider-item:hover .trainers__slider-wrapper {
  background-color: #bef52b;
  box-shadow: 0 1rem 2rem -0.2rem rgba(0, 0, 0, 0.2);
}
.trainers__slider-item:hover .trainers__button {
  background-color: #fff;
  bottom: 1.5rem;
  opacity: 1;
}
.trainers__slider-wrapper {
  margin-left: .8rem;
  margin-right: .8rem;
  height: 100%;
  background-color: #fff;
  border-radius: .5rem;
  padding: 1.4rem 1.3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transition: .3s;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}
.trainers .swiper-wrapper {
  align-items: stretch!important;
}
.trainers__info {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.trainers__name {
  width: 100%;
  font-size: 1.1rem;
  font-weight: 700;
}
.trainers__job {
  width: 100%;
  font-size: .87rem;
  font-weight: 300;
  margin-bottom: .8rem;
}
.trainers__button {
  position: absolute;
  left: 50%;
  bottom: -20%;
  transform: translateX(-50%);
  opacity: 0;
}
/*Media*/
.media {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.media__wrapper {
  justify-content: space-between;
  align-items: flex-end;
}
.media__wrapper > div:first-of-type {
  width: 58%;
}
.media__wrapper > div:last-of-type {
  width: 32%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.media__title {
  margin-bottom: 4rem;
}
.media__video {
  width: 100%;
  height: 23rem;
  background: url(../img/video-prev.jpg) no-repeat center / cover;
  position: relative;
  cursor: pointer;
}
.media__video-play {
  display: block;
  width: 4.2rem;
  height: 4.2rem;
  background: rgba(255, 255, 255, 0.2) url(../img/play.svg) no-repeat center / 1.3rem;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: .3s;
}
.media__video-play:hover {
  width: 4.7rem;
  height: 4.7rem;
}
.media__subtitle {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 1.5rem;
  line-height: 120%;
  font-weight: 700;
  text-align: right;
  padding-left: 2.1rem;
  background: url(../img/media-inst.svg) no-repeat left 0.4rem;
  background-size: 1.2rem;
  margin-bottom: 1.5rem;
}
.media__subtitle a {
  font-size: 1rem;
  color: #323a45;
  transition: .3s;
}
.media__subtitle a:hover {
  color: #bef52b;
}
.media__inst {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -0.5rem;
}
.media__inst-item {
  display: block;
  width: calc(33.333% - .6rem);
  height: 7rem;
  margin: .3rem;
  position: relative;
  overflow: hidden;
}
.media__inst img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  max-width: 110%;
}
/*Cards*/
.cards {
  padding-top: 3rem;
  padding-bottom: 8rem;
  position: relative;
  overflow: hidden;
}
.cards__title {
  margin-bottom: 2rem;
}
.cards__slider {
  margin-left: calc(50% - 36rem - 30px);
  padding-left: 30px;
}
.cards__slider-item {
  width: 34.4rem;
  padding-bottom: 4rem;
  padding-top: 2rem;
  overflow: hidden;
}
.cards__slider-item:hover .cards__slider-wrapper {
  background-color: #fff;
  box-shadow: 0 0.5rem 2rem -0.2rem rgba(0, 0, 0, 0.2);
}
.cards__slider-item:hover .cards__paragraph span {
  color: #bef52b;
}
.cards__slider-item:hover .cards__button {
  background-color: #ddd;
  border-color: #ddd;
}
.cards__slider-wrapper {
  margin-left: .8rem;
  margin-right: .8rem;
  background-color: #ededed;
  border-radius: .5rem;
  padding: 2.2rem 0 2.2rem 1.8rem;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  transition: .3s;
  cursor: pointer;
  overflow: hidden;
}
.cards__info {
  width: 48%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.cards__paragraph {
  width: 49%;
  font-size: .8rem;
  line-height: 150%;
  color: #7a7d84;
  margin-bottom: 1rem;
}
.cards__paragraph span {
  display: block;
  font-size: 1rem;
  font-weight: 500;
  color: #323a45;
  transition: .3s;
}
.cards__button {
  background-color: transparent;
  border: 1px solid #333;
  min-width: 70%;
  padding-top: .7rem;
  padding-bottom: .7rem;
}
.cards__cover {
  color: #fff;
  width: 50%;
  min-height: 11rem;
  border-radius: .5rem 0 0 .5rem;
  padding: 1.8rem .5rem 1.8rem 1.8rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-shadow: 1.5rem 1.5rem 2rem -0.8rem rgba(0, 0, 0, 0.23);
  position: relative;
  overflow: hidden;
  background-image: url(../img/logo-cards.png);
  background-size: 3rem;
  background-repeat: no-repeat;
  background-position: top 1.8rem left 1.8rem;
}
.cards__cover::before {
  content: '';
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
  position: absolute;
  left: 0;
  bottom: 0;
}
.cards__cover::after {
  content: "";
  width: 3.9rem;
  height: 16rem;
  transform: rotate(45deg);
  position: absolute;
  background: linear-gradient(transparent, rgba(255, 255, 255, 0.2));
  right: .7rem;
  bottom: -50%;
}
.cards__name {
  font-size: 1.5rem;
  font-weight: 500;
  word-break: break-word;
  position: relative;
}
.cards__description {
  font-size: .87rem;
  font-weight: 300;
  position: relative;
  margin-top: 1rem;
}
/*Gallery*/
.gallery {
  background-color: #ededed;
  padding-top: 5rem;
  padding-bottom: 8rem;
  position: relative;
}
.gallery__title {
  margin-bottom: 4rem;
}
.gallery__slider {
  width: 100%;
  padding-bottom: 4rem;
  padding-left: 30px;
}
.gallery__slider-item {
  width: 28rem;
  display: flex;
  flex-direction: column;
}
.gallery__image {
  width: 100%;
  height: 18.7rem;
  border-radius: .5rem;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}
.gallery__image:first-of-type {
  margin-bottom: 30px;
}
.gallery__image:last-of-type {
  transform: translateX(-50%);
}
.gallery__image::after {
  content: '';
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5) url(../img/expand.svg) no-repeat center / 15%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: .3s;
  opacity: 0;
}
.gallery__image:hover::after {
  opacity: 1;
}
.gallery__image img {
  max-height: 110%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  cursor: pointer;
}
/*About*/
.about {
  padding-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
.about__wrapper {
  position: relative;
}
.about__title {
  margin-bottom: 7.5rem;
}
.about__image {
  width: 100%;
}
.about__text {
  width: 60%;
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1rem;
  line-height: 150%;
  background-color: #fff;
  border-radius: .5rem;
  padding: 3rem;
  box-shadow: 0 1rem 2rem 0.3rem rgba(0, 0, 0, 0.2);
}
/*Contact*/
.contact {
  padding-bottom: 5rem;
  position: relative;
}
.contact__map {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.contact #map {
  width: 100%;
  height: 100%;
}
.contact .form {
  width: 28rem;
  padding: 3.8rem;
  border-radius: .5rem;
  margin-top: -6rem;
}
.contact .form__title {
  font-size: 1.6rem;
  margin-bottom: 2rem;
}
.contact .form__button {
  width: 100%;
}
/*Footer*/
.footer {
  padding-top: 1.8rem;
  padding-bottom: 1.8rem;
}
.footer__wrapper {
  justify-content: space-between;
  align-items: center;
}
.footer__wrapper > p,
.footer__wrapper > a:not(.footer__up) {
  padding-left: 2.8rem;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 1.4rem;
}
.footer__wrapper > a:first-of-type {
  background-image: url(../img/footer-adress.svg);
}
.footer__wrapper > p:nth-of-type(1) {
  background-image: url(../img/footer-time.svg);
}
.footer__wrapper > p:nth-of-type(2) {
  background-image: url(../img/footer-phone.svg);
}
.footer p {
  font-size: 1rem;
  line-height: 150%;
}
.footer span,
.footer a {
  font-weight: 500;
  display: block;
}
.footer a {
  color: #333;
  transition: .3s;
}
.footer a:hover {
  opacity: .7;
}
.footer__up {
  display: block;
  width: 2.5rem;
  height: 2.5rem;
  background: #bef52b url(../img/chevron-up.svg) no-repeat center / 1.1rem;
  border-radius: 50%;
}
.social {
  display: flex;
}
.social a {
  display: block;
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 50%;
  margin-left: .7rem;
  margin-right: .7rem;
  transition: .3s;
}
.social a:nth-of-type(1) {
  background: #3d5a96 url(../img/soc-fb.svg) no-repeat center / 1.4rem;
}
.social a:nth-of-type(2) {
  background: #4c77a6 url(../img/soc-vk.svg) no-repeat center / 1.1rem;
}
.social a:nth-of-type(3) {
  background: linear-gradient(180.19deg, #7024c4 0.17%, #c21975 41.57%, #c74c4d 70.15%, #e09b3d 99.83%);
  background-repeat: no-repeat;
  position: relative;
}
.social a:nth-of-type(3)::after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: url(../img/soc-inst.svg) no-repeat center / 1.1rem;
}
.social a:hover {
  opacity: .7;
}
/*Forms*/
.form {
  position: relative;
  z-index: 3;
}
.form_dark {
  background-color: #323a45;
  color: #fff;
}
.form_light {
  background-color: #fff;
}
.form__input {
  width: 100%;
  font-size: 1rem;
  color: #333;
  padding: 1rem 1.2rem;
  background-color: #fff;
  border: 1px solid #bbbbbb;
  border-radius: 3rem;
}
.form__input-wrap {
  display: block;
  width: 100%;
  position: relative;
  margin-bottom: 1.8rem;
}
.form__input-wrap.active .form__input-text {
  font-size: .6rem;
  top: 20%;
  opacity: .7;
}
.form__input-text {
  font-size: 1rem;
  color: #333;
  position: absolute;
  left: 1.2rem;
  top: 50%;
  transform: translateY(-50%);
  transition: .3s;
}
.form__agreement {
  width: 100%;
  display: block;
  margin-bottom: 1.8rem;
}
.form__agreement-text {
  font-size: .8rem;
  position: relative;
  padding-left: 2rem;
  cursor: pointer;
  user-select: none;
}
.form__agreement-text a {
  color: #bfbfbf;
  text-decoration: underline;
}
.form__agreement-text::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  border: 2px solid #bef52b;
  border-radius: 50%;
  background-color: transparent;
}
.form__agreement-text::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: .7rem;
  height: .7rem;
  border-radius: 50%;
  background: none;
}
.form__agreement-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.form__agreement .registrate__agreement-checkbox:checked + .registrate__agreement-text::after {
  background: #bef52b;
}
.form__button {
  cursor: pointer;
}
/*Modal*/
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 12;
  background: rgba(0, 0, 0, 0.8);
}
.modal__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
.modal__wrapper {
  background-color: #fff;
  border-radius: .5rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) scale(0.3, 0.3);
  width: 32rem;
  max-height: 90vh;
  overflow: auto;
  justify-content: center;
  transition: .5s;
  opacity: 0;
}
.modal__wrapper::-webkit-scrollbar {
  width: 5px;
  -webkit-appearance: none;
}
.modal__wrapper::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #bef52b;
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}
.modal__wrapper.active {
  opacity: 1;
  transform: translateX(-50%) translateY(-50%) scale(1, 1);
  z-index: 12;
}
.modal_stocks .modal__header {
  height: 18rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.modal_stocks .modal__title {
  opacity: 0;
  transform: scale(0, 0);
  height: 0;
}
.modal_services .modal__additional,
.modal_trainers .modal__additional,
.modal_services .modal__additional-field,
.modal_trainers .modal__additional-field {
  opacity: 1;
  transform: scale(1, 1);
  height: initial;
}
.modal_cards .modal__title {
  margin-bottom: 2rem;
  text-align: center;
}
.modal_cards .modal__card {
  opacity: 1;
  transform: scale(1, 1);
  min-height: 10.6rem;
  height: initial;
  padding: 1.8rem;
  display: flex;
}
.modal__widget {
  max-height: 90vh;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%) scale(0.3, 0.3);
  transition: .5s;
  opacity: 0;
}
.modal__widget.active {
  opacity: 1;
  transform: translateX(-50%) translateY(-50%) scale(1, 1);
  z-index: 12;
}
.modal_video {
  width: 80%;
  max-width: 60rem;
}
.modal_video .modal__video {
  opacity: 1;
  transform: scale(1, 1);
  height: initial;
}
.modal_gallery {
  width: 80%;
  max-width: 60rem;
}
.modal_gallery .modal__slider {
  opacity: 1;
  height: initial;
}
.modal__special {
  width: 100%;
  height: 100%;
  background-color: #ededed;
  overflow: auto;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(-300%);
  transition: .5s;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal__special::-webkit-scrollbar {
  width: 5px;
  -webkit-appearance: none;
}
.modal__special::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #bef52b;
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}
.modal__special::after {
  content: '';
  width: 7.2rem;
  height: 2.7rem;
  background: url(../img/logo-modal.png) no-repeat center / contain;
  position: absolute;
  left: 2rem;
  top: 2rem;
}
.modal__special.active {
  opacity: 1;
  transform: none;
  z-index: 12;
}
.modal__special .modal__container {
  width: 100%;
  max-width: 75rem;
}
.modal__special .modal__header {
  padding-bottom: 0;
  padding-top: 0;
}
.modal__special .modal__title {
  font-size: 2.2rem;
  text-align: center;
  padding-bottom: 3.5rem;
  margin-bottom: 0;
}
.modal__special-row {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  margin-left: auto;
  margin-right: auto;
  padding-top: 3.5rem;
  padding-bottom: 3.5rem;
  border-top: 1px dashed #323a45;
  border-bottom: 1px dashed #323a45;
}
.modal__special-item {
  width: 31.5%;
  background-color: #fff;
  border-radius: .5rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 150%;
  padding: 2rem;
  cursor: pointer;
  border: 2px solid #fff;
}
.modal__special-item span {
  display: block;
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 1.2rem;
}
.modal__special-item.active {
  border-color: #bef52b;
  box-shadow: 0 0.5rem 2rem -0.2rem rgba(0, 0, 0, 0.15);
}
.modal__special .modal__form {
  width: 100%;
  background-color: #ededed;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
  margin-bottom: 3rem;
}
.modal__special .form__input-wrap {
  margin-bottom: 0;
  margin-right: 3%;
}
.modal__special .form__button {
  min-width: 30%;
  margin-bottom: 0!important;
}
.modal__special .form__agreement {
  position: absolute;
  margin-bottom: 0;
  bottom: -3.5rem;
  left: 3.5rem;
  width: initial;
}
.modal__header {
  background-color: #ededed;
  background-image: none;
  padding: 3.5rem;
}
.modal__title {
  font-size: 1.6rem;
  margin-bottom: 1.2rem;
  transition: .3s;
}
.modal__additional {
  display: block;
  opacity: 0;
  transform: scale(0, 0);
  height: 0;
  transition: .3s;
  font-size: .87rem;
  font-weight: 500;
  color: #949494;
  margin-bottom: .5rem;
}
.modal__additional-field {
  display: block;
  opacity: 0;
  height: 0;
  transform: scale(0, 0);
  transition: .3s;
  font-size: 1rem;
  font-weight: 500;
}
.modal__card {
  opacity: 0;
  transform: scale(0, 0);
  transition: .3s;
  width: 80%;
  display: none;
  height: 0;
  padding: 0;
  overflow: hidden;
  margin: 0 auto;
  border-radius: .5rem;
}
.modal__form {
  padding: 3.5rem 3.5rem 0 3.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.modal__form .form__button {
  margin-bottom: 3.5rem;
}
.modal__video {
  display: flex;
  width: 100%;
  opacity: 0;
  transform: scale(0, 0);
  height: 0;
}
.modal__slider {
  opacity: 0;
  height: 0;
  position: relative;
}
.modal__slider-item {
  overflow: hidden;
  height: 40rem;
  width: 100%!important;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
}
.modal__slider img {
  min-height: 100%;
  max-height: 110%;
}
.modal__arrow {
  top: 50%;
  transform: translateY(-50%);
}
.modal__arrow-prev {
  left: 2rem;
}
.modal__arrow-next {
  right: 2rem;
}
.modal__close {
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  background: rgba(0, 0, 0, 0.3) url(../img/close.svg) no-repeat center / 1rem;
  border-radius: 50%;
  position: absolute;
  top: 1.2rem;
  right: 1.2rem;
  z-index: 2;
}
